: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\n\nbody,\nhtml {\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  height: 100%;\n}\n\nbody {\n  font-family: 'Quicksand', sans-serif;\n  ;\n  font-size: 12px;\n  color: black;\n}\n\n:host {\n  display: block;\n  margin-bottom: 18px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  border-radius: 10px;\n  overflow: hidden;\n}\n\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  padding: 8px;\n  background-color: black;\n  color: white;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n.header {\n  min-height: 56px;\n  transition: min-height 0.3s;\n}\n\n.header_inner {\n  width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n  display: flex;\n  justify-content: center;\n}\n\n.header_logo img {\n  width: 150px;\n  height: 75px;\n}\n\n.nav {\n  width: 100%;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.nav_item {\n  box-sizing: border-box;\n  display: inline-block;\n  width: 24%;\n  text-align: center;\n  line-height: 24px;\n  padding: 24px;\n  text-transform: uppercase;\n}\n\n.nav_list {\n  width: 100%;\n  padding: 0;\n  margin: 0;\n  text-align: center;\n}\n\n.nav a {\n  text-decoration: none;\n  color: #616161;\n}\n\n.nav a:hover {\n  text-decoration: underline;\n  color: #000;\n}\n\n.hero {\n  display: flex;\n  align-items: center;\n  min-height: 380px;\n  width: 100%;\n  text-align: center;\n  background-image: url(\"../public/images/heros/hero-image_2.jpg\");\n  background-position: center;\n  background-color: green;\n}\n\n.hero_inner {\n  margin: 0 auto;\n  max-width: 100%;\n}\n\n.hero_title {\n  color: black;\n  text-shadow:\n    -1px -1px 0 #fff,\n    1px -1px 0 #fff,\n    -1px 1px 0 #fff,\n    1px 1px 0 #fff;\n  font-weight: 500;\n  font-size: 36px;\n}\n\n.hero_desc {\n  color: black;\n  text-shadow:\n    -1px -1px 0 #fff,\n    1px -1px 0 #fff,\n    -1px 1px 0 #fff,\n    1px 1px 0 #fff;\n  margin-top: 16px;\n  font-size: 18px;\n  font-weight: 300;\n}\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.content {\n  padding: 32px;\n}\n\n.latest {\n  width: 100%;\n  margin: 60px auto;\n  text-align: center;\n}\n\n.latest_label {\n  font-size: 32px;\n  font-weight: lighter;\n}\n\n.latest_label::after {\n  content: '';\n  margin-top: 16px;\n  display: block;\n  border-bottom: 1px solid #eeeeee;\n}\n\n.list-item {\n  margin: 32px auto auto;\n  text-align: left;\n  overflow: auto;\n}\n\n/*\n    Posts\n  */\n\n.posts {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 16px;\n  margin: 32px 0;\n}\n\n.restaurant-item {\n  margin: 16px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  width: auto;\n  border-radius: 5px;\n  overflow: hidden;\n}\n\n.restaurant-item_header {\n  position: relative;\n}\n\n.restaurant-item .restaurant-item_header .restaurant-item_header_poster {\n  width: 100%;\n}\n\n.restaurant-item .restaurant-item_header .restaurant-item_header_rating {\n  position: absolute;\n  padding: 8px;\n  bottom: 20px;\n  left: 0;\n  display: inline-block;\n  background-color: black;\n  color: white;\n}\n\n.restaurant-item .restaurant-item_header .restaurant-item_header_rating .restaurant-item_header_rating_score {\n  margin-left: 10px;\n}\n\n.restaurant-item .restaurant-item_content {\n  padding: 16px;\n}\n\n.restaurant-item .restaurant-item_content h3 {\n  margin: 0 0 10px 0;\n}\n\n.restaurant-item .restaurant-item_content h3 a {\n  color: #db0000;\n  text-decoration: none;\n  height: 44px;\n  width: 44px;\n  font-size: 2.2rem;\n  line-height: 44px;\n}\n\n.restaurant-item .restaurant-item_content p {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4;\n  /* number of lines to show */\n  -webkit-box-orient: vertical;\n}\n\n/*\n    Restaurant Detail\n  */\n\n.restaurant {\n  margin: 32px auto;\n  width: 100%;\n}\n\n.restaurant .restaurant_poster {\n  width: 100%;\n  max-width: 400px;\n}\n\n.restaurant .restaurant_info h4 {\n  margin: 8px 0;\n}\n\n.restaurant_title {\n  font-size: 32px;\n}\n\n.restaurant_menus {\n  margin: 16px;\n  padding: 10px;\n  width: auto;\n  border-radius: 5px;\n  overflow: hidden;\n}\n\n.restaurant_drink {\n  margin: 16px;\n  padding: 10px;\n  /* box-shadow: 0 4px 8px 0 rgba(0,0,0,.2); */\n  width: auto;\n  border-radius: 5px;\n  overflow: hidden;\n}\n\n.restaurant_menus h3 {\n  display: flex;\n  align-items: center;\n  text-align: center;\n}\n\n.restaurant_menus ul {\n  margin: 10px;\n}\n\n.restaurant_drink ul {\n  margin: 10px;\n}\n\n.restaurant_review h3 {\n  font-size: 20px;\n}\n\n.review_item {\n  margin: 16px;\n  padding: 10px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, .2);\n  width: auto;\n  border-radius: 5px;\n  overflow: hidden;\n  display: block;\n}\n\n/*\n  Like\n*/\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #000000;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 1;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n  border-color: #fff;\n}\n\nfooter {\n  background-color: black;\n  padding: 2em;\n  width: 100%;\n  text-align: center;\n}\n\n.foot {\n  text-decoration: none;\n  color: #fff;\n  min-width: 44px;\n  min-height: 44px;\n}\n\nimg {\n  display: block;\n}"],"sourceRoot":""}]);
